ng-bottom {\n    display: inline-block;\n    overflow: hidden;\n    position: relative;\n    min-width: 0px;\n}\n\n\n/**\n ****************************\n * Dialog\n ****************************\n*/\n\n.ag-dialog, .ag-panel {\n    display: flex;\n    flex-direction: column;\n    position: relative;\n    overflow: hidden;\n}\n\n.ag-panel-title-bar {\n    display: flex;\n    flex: none;\n    align-items: center;\n    cursor: default;\n}\n\n.ag-panel-title-bar-title {\n    flex: 1 1 auto;\n}\n\n.ag-panel-title-bar-buttons {\n    display: flex;\n}\n\n.ag-panel-title-bar-button {\n    cursor: pointer;\n}\n\n.ag-panel-content-wrapper {\n    display: flex;\n    flex: 1 1 auto;\n    position: relative;\n    overflow: hidden;\n}\n\n.ag-dialog {\n    position: absolute;\n}\n\n.ag-resizer {\n    position: absolute;\n    pointer-events: none;\n    z-index: 1;\n    &.ag-resizer-topLeft {\n        top: 0;\n        left: 0;\n        height: 5px;\n        width: 5px;\n        cursor: nwse-resize;\n    }\n    &.ag-resizer-top {\n        top: 0;\n        left: 5px;\n        right: 5px;\n        height: 5px;\n        cursor: ns-resize;\n    }\n    &.ag-resizer-topRight {\n        top: 0;\n        right: 0;\n        height: 5px;\n        width: 5px;\n        cursor: nesw-resize;\n    }\n    &.ag-resizer-right {\n        top: 5px;\n        right: 0;\n        bottom: 5px;\n        width: 5px;\n        cursor: ew-resize;\n    }\n    &.ag-resizer-bottomRight {\n        bottom: 0;\n        right: 0;\n        height: 5px;\n        width: 5px;\n        cursor: nwse-resize;\n    }\n    &.ag-resizer-bottom {\n        bottom: 0;\n        left: 5px;\n        right: 5px;\n        height: 5px;\n        cursor: ns-resize;\n    }\n    &.ag-resizer-bottomLeft {\n        bottom: 0;\n        left: 0;\n        height: 5px;\n        width: 5px;\n        cursor: nesw-resize;\n    }\n    &.ag-resizer-left {\n        left: 0;\n        top: 5px;\n        bottom: 5px;\n        width: 5px;\n        cursor: ew-resize;\n    }\n}\n\n/**\n ****************************\n * Tooltip\n ****************************\n*/\n\n.ag-tooltip {\n    position: absolute;\n    pointer-events: none;\n    z-index: 99999;\n}\n\n/**\n ****************************\n * Animations\n ****************************\n*/\n\n// this is used by the animateShowChangeCellRenderer. it is arguable that this belongs in the themes,\n// however it is not tied to color, only placement and visiblity, which is behaviour and not style,\n// thus belongs here, besides it doesn't change wih the themes\n.ag-value-slide-out {\n    margin-right: 5px;\n    opacity: 1;\n    transition: opacity 3s, margin-right 3s; // as value fades, it also moves to the left via the margin setting\n    transition-timing-function: linear;\n}\n\n.ag-value-slide-out-end {\n    margin-right: 10px;\n    opacity: 0;\n}\n\n.ag-opacity-zero {\n    opacity: 0 !important;\n}\n\n/**\n ****************************\n * Menu\n ****************************\n*/\n.ag-menu {\n    max-height: 100%;\n    overflow-y: auto;\n    position: absolute;\n    user-select: none;\n}\n\n.ag-menu-column-select-wrapper {\n    height: 265px;\n    overflow: auto;\n\n    .ag-column-select {\n        height: 100%;\n    }\n}\n\n.ag-menu-list {\n    display: table;\n    width: 100%;\n}\n\n.ag-menu-option, .ag-menu-separator {\n    display: table-row;\n}\n\n.ag-menu-option-part, .ag-menu-separator-part {\n    display: table-cell;\n    vertical-align: middle;\n}\n\n.ag-menu-option-text {\n    white-space: nowrap;\n}\n\n.ag-compact-menu-option {\n    width: 100%;\n    display: flex;\n    flex-wrap: nowrap;\n}\n\n.ag-compact-menu-option-text {\n    white-space: nowrap;\n    flex: 1 1 auto;\n}\n\n/**\n ****************************\n * Rich Select\n ****************************\n*/\n.ag-rich-select {\n    cursor: default;\n    outline: none;\n}\n\n.ag-rich-select-value {\n    display: flex;\n    align-items: center;\n}\n\n.ag-rich-select-value-icon {\n    flex: 1 1 auto;\n    order: 1;\n