       right: 7px;\n        }\n        &.datepicker-orient-bottom:before {\n            top: -7px;\n        }\n        &.datepicker-orient-bottom:after {\n            top: -6px;\n        }\n\n        &.datepicker-orient-top:before {\n            bottom: -7px;\n            border-bottom: 0;\n            border-top: 7px solid $datepicker-dropdown-border;\n        }\n\n        &.datepicker-orient-top:after {\n            bottom: -6px;\n            border-bottom: 0;\n            border-top: 6px solid $datepicker-dropdown-bg;\n        }\n    }\n\n    table {\n        margin: 0;\n        -webkit-touch-callout: none;\n        user-select: none;\n\n        tr {\n            td {\n                border-radius: $datepicker-cell-border-radius;\n            }\n\n            th {\n                border-radius: $datepicker-header-cell-border-radius;\n                font-weight: 500;\n            }\n\n            td,\n            th {\n                transition: $datepicker-cell-transition;\n                width: $datepicker-cell-width;\n                height: $datepicker-cell-height;\n                border: none;\n                text-align: center;\n                font-size: $font-size-sm;\n            }\n        }\n    }\n\n    // Inline display inside a table presents some problems with\n    // border and background colors.\n    .table-striped & table tr {\n        td,\n        th {\n            background-color: transparent;\n        }\n    }\n\n    table tr td {\n        &.old,\n        &.new {\n            color: $datepicker-disabled-old-new-color;\n        }\n\n        &.day:hover,\n        &.focused {\n            background: $datepicker-cell-hover-background;\n            cursor: pointer;\n        }\n\n        &.disabled,\n        &.disabled:hover {\n            background: none;\n            color: $datepicker-disabled-cell-color;\n            cursor: default;\n        }\n\n        &.highlighted {\n            border-radius: 0;\n\n            &.focused {\n                background: $datepicker-highlighted-bg\n            }\n\n            &.disabled,\n            &.disabled:active {\n                background: $datepicker-highlighted-bg;\n                color: $gray-400;\n            }\n        }\n\n        &.today {\n            background: lighten($datepicker-active-background, 45%);\n            &.focused {\n                background: lighten($gray-400, 58%);\n            }\n\n            &.disabled,\n            &.disabled:active {\n                background: lighten($gray-400, 58%);\n                color: $btn-link-disabled-color;\n            }\n        }\n\n        // Range selection\n        &.range {\n            background: $datepicker-range-background;\n            color: $datepicker-range-color;\n            border-radius: 0;\n\n            &.focused {\n                background: darken($datepicker-range-cell-focused-background, 3%);\n            }\n\n            &.disabled,\n            &.disabled:active,\n            &.day.disabled:hover {\n                background: darken($datepicker-active-background, 10%);\n                color: lighten($datepicker-active-background, 10%);\n            }\n        }\n\n        &.range.highlighted {\n            &.focused {\n                background: darken($datepicker-range-highlighted-bg, 10%);\n            }\n\n            &.disabled,\n            &.disabled:active {\n                background: $datepicker-range-highlighted-bg;\n                color: $datepicker-disabled-cell-color;\n            }\n        }\n\n        &.range.today {\n            &.disabled,\n            &.disabled:active {\n                background: $blue;\n                color: $white;\n            }\n        }\n\n        &.day.range-start {\n            border-top-right-radius: 0;\n            border-bottom-right-radius: 0;\n        }\n\n        &.day.range-end {\n            border-top-left-radius: 0;\n            border-bottom-left-radius: 0;\n        }\n\n        &.day.range-start.range-end {\n            border-radius: $datepicker-cell-border-radius;\n        }\n\n        &.selected,\n        &.selected.highlighted,\n        &.selected:hover,\n        &.selected.highlighted:hover,\n        &.day.range:hover {\n            background: $datepicker-active-background;\n            color: $datepicker-active-color;\n        }\n\n        &.active,\n        &.active.highlighted,\n        &.active:hover,\n        &.active.highlighted:hover {\n            background: $datepicker-active-background;\n            color: $datepicker-active-color;\n            box-shadow: $datepicker-active-box-shadow;\n        }\n\n        span {\n            display: block;\n            width: 23%;\n            height: 54px;\n            line-height: 54px;\n            float: left;\n            margin: 1%;\n            cursor: pointer;\n            border-radius: 4px;\n\n            &:hover,\n            &.focused {\n                background: $gray-200;\n            }\n\n            &.disabled,\n            &.disabled:hover {\n                background: none;\n                color: $datepicker-disabled-cell-color;\n                cursor: default;\n            }\n\n            &.active,\n            &.active:hover,\n            &.active.disabled,\n            &.active.disabled:hover {\n                text-shadow: 0 -1px 0 rgba(0, 0, 0, .25);\n            }\n\n            &.old,\n            &.new {\n                color: $btn-link-disabled-color;\n            }\n        }\n    }\n\n    .datepicker-switch {\n        width: 145px;\n    }\n\n    .datepicker-switch,\n    .prev,\n    .next,\n    tfoot tr th {\n        cursor: pointer;\n        &:hover {\n            background: $gray-200;\n        }\n    }\n\n    .prev,\n    .next {\n        &.disabled {\n            visibility: hidden;\n        }\n    }\n\n    // Basic styling for calendar-week cells\n    .cw {\n        font-size: 10px;\n        width: 12px;\n        padding: 0 2px 0 5px;\n        vertical-align: middle;\n    }\n}\n\nngb-datepicker,\nngb-datepicker .ngb-dp-header,\nngb-datepicker .ngb-dp-month,\nngb-datepicker .ngb-dp-month-name,\nngb-datepicker .ngb-dp-day,\nngb-datepicker .ngb-dp-week-number,\nngb-datepicker .ngb-dp-weekday,\nngb-datepicker .ngb-dp-weekdays{\n  background: $white;\n  text-align: center;\n}\n\nngb-datepicker .ngb-dp-month .ngb-dp-week .ngb-dp-day{\n  font-size: .875rem;\n  width: 36px;\n  height: 36px;\n  transition: all .15s ease;\n  text-align: center;\n  border: none;\n  border-radius: 50%;\n}\n\nngb-datepicker .ngb-dp-months .ngb-dp-week .ngb-dp-day .btn-light,\nngb-datepicker .ngb-dp-months  .ngb-dp-week .ngb-dp-day .custom-day {\n    width: 36px;\n    height: 36px;\n    box-shadow: none;\n    color: #525f7f;\n    border-radius: 50%;\n    align-items: center;\n    display: grid;\n}\n\nngb-datepicker .ngb-dp-months.ngb-dp-week .ngb-dp-day .btn-light.range-start,\nngb-datepicker .ngb-dp-months .ngb-dp-week .ngb-dp-day .custom-day.range-start {\n    border-top-right-radius: 0;\n    border-bottom-right-radius: 0;\n}\n\nngb-datepicker .ngb-dp-months .ngb-dp-week .ngb-dp-day .btn-light.range,\nngb-datepicker .ngb-dp-months .ngb-dp-week .ngb-dp-day .custom-day.range {\n    color: #fff;\n}\n\nngb-datepicker .ngb-dp-months .ngb-dp-week .ngb-dp-day .btn-light.faded,\nngb-datepicker .ngb-dp-months .ngb-dp-week .ngb-dp-day .custom-day.faded{\n  border-radius: 0;\n}\n\nngb-datepicker .ngb-dp-months .ngb-dp-week .ngb-dp-day .btn-light.range-end,\nngb-datepicker .ngb-dp-months .ngb-dp-week .ngb-dp-day .custom-day.range-end {\n    border-top-left-radius: 0;\n    border-bottom-left-radius: 0;\n}\n\n.ngb-dp-week.ngb-dp-weekdays{\n  border: none;\n}\n\nngb-datepicker .ngb-dp-weekday{\n  color: $primary;\n  font-style: normal;\n  font-weight: 300;\n  font-size: 14px;\n  width: 36px;\n}\n","//\n// NoUi Slider\n//\n\n\n// Original styles\n\n.noUi-target,\n.noUi-target * {\n    -webkit-touch-callout: none;\n    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);\n    -webkit-user-select: none;\n    touch-action: none;\n    user-select: none;\n    box-sizing: border-box;\n}\n\n.noUi-target {\n    position: relative;\n    direction: ltr;\n}\n\n.noUi-base,\n.noUi-connects {\n    width: 100%;\n    height: 100%;\n    position: relative;\n    z-index: 1;\n}\n\n\n// Wrapper for all connect elements\n\n.noUi-connects {\n    overflow: hidden;\n    z-index: 0;\n}\n\n.noUi-connect,\n.noUi-origin {\n    will-change: transform;\n    position: absolute;\n    z-index: 1;\n    top: 0;\n    left: 0;\n    height: 100%;\n    width: 100%;\n    transform-origin: 0 0;\n}\n\n\nhtml:not([dir=\"rtl\"]) .noUi-horizontal .noUi-origin {\n    right: 0;\n    width: 10%;\n    left: auto;\n}\n\n.noUi-vertical .noUi-origin {\n    width: 0;\n}\n\n.noUi-horizontal .noUi-origin {\n    height: 0;\n}\n\n.noUi-handle {\n    position: absolute;\n}\n\n.noUi-state-tap .noUi-connect,\n.noUi-state-tap .noUi-origin {\n    transition: transform .3s;\n}\n\n.noUi-state-drag * {\n    cursor: inherit !important;\n}\n\n.noUi-horizontal {\n    height: $noui-target-thickness;\n}\n\n.noUi-horizontal .noUi-handle {\n    width: 34px;\n    height: 28px;\n    left: -17px;\n    top: -6px;\n}\n\n.noUi-vertical {\n    width: $noui-target-thickness;\n}\n\n.noUi-vertical .noUi-handle {\n    width: 28px;\n    height: 34px;\n    left: -6px;\n    top: -17px;\n}\n\nhtml:not([dir=\"rtl\"]) .noUi-horizontal .noUi-handle {\n    right: -17px;\n    left: auto;\n}\n\n.noUi-connects {\n    border-radius: 3px;\n}\n\n.noUi-connect {\n    background: $noui-slider-connect-bg;\n}\n\n.noUi-draggable {\n    cursor: ew-resize;\n}\n\n.noUi-vertical .noUi-draggable {\n    cursor: ns-resize;\n}\n\n.noUi-handle {\n    border: 1px solid #D9D9D9;\n    border-radius: 3px;\n    background: #FFF;\n    cursor: default;\n    box-shadow: inset 0 0 1px #FFF, inset 0 1px 7px #EBEBEB, 0 3px 6px -3px #BBB;\n    outline: none;\n}\n\n.noUi-active {\n    outline: none;\n}\n\n\n/* Disabled state;\n */\n\n[disabled] .noUi-connect 